import React from 'react'
import '../css/Navbar.css'
function Navbar() {
  return (
    <React.Fragment>
      <header>
          <div className="navbar-fixed">
          <nav>
            <div className="nav-wrapper navbar-div">
              <a href="/" className="brand-logo">Logo</a>
              <a  className="sidenav-trigger right" data-target="mobile-menu">
                <i className="material-icons ">menu</i>
              </a>
              <ul className="right hide-on-med-and-down">
                <li><a href="/">Services</a></li>
                <li><a href="#section2">Chattel Learnings</a></li>
                <li><a href="#section3">Chattel Certifications</a></li>
                <li><a href="#section4">About us</a></li>
                <li><a href="#section5">Contact</a></li>
            
              </ul>
            </div>

            <ul className="sidenav navbar-fixed" id="mobile-menu">
                <li><a href="/">Services</a></li>
                <li><a href="#section2">Chattel Learnings</a></li>
                <li><a href="#section3">Chattel Certifications</a></li>
                <li><a href="#section4">About us</a></li>
                <li><a href="#section5">Contact</a></li>
            
            </ul>

           

          </nav>
        </div>
         
          


        




      </header>

    </React.Fragment>
  )
}

export default Navbar
